.container-heading{
    display: flex;
    justify-content: center;
    align-items: center;
}


#heading{
    -webkit-text-stroke: 1px #4e5250;
    color: aliceblue;
    letter-spacing: 8px;
    position: relative;
    font-size: 3.5rem;
    font-weight: 650;
}

#heading::before{
    content: attr(datatype);
    position: absolute;
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    color: #e12d12;
    -webkit-text-stroke: 0vw #4e5250;
    border-right: 5px solid #e12d12;
    overflow: hidden;
    animation: text 6s linear infinite;
}

@keyframes text {
    0% {
        width: 0;
    }
     70% {
        width: 100%;
    }
}
@media screen and (max-width: 870px) {
    #heading{
        font-size: 3.5rem;
    }
}
@media screen and (max-width: 600px) {
    #heading{
        font-size: 2.5rem;
    }
}