.card{
    background: #c1d0d6e0;
    border: 6px solid ;
    color: rgb(84, 35, 35);
    border-image: linear-gradient( 
        #0e35d2,
        #C700AE,
        #FF217E 
        /* #0FDCE4,
        #0fdde4aa,
        #0fdde47e */
    ) 1;
    
    animation: rotate 2.5s ease-in infinite;
}

@keyframes rotate {
    50%{
        border-image: linear-gradient(
        360deg,
        #0e35d2,
        #FF217E,
        #C700AE
    ) 1;
    }
}